@use 'base/variables' as v;


.citas{
    list-style: none;
    padding: 0;
    li{
        padding: 1rem;
        border-top: 1px solid v.$blanco;

        p{
            font-weight: bold;
            color: v.$azul;
        }

        span,
        .servicio{
            font-weight:400;
            color: v.$blanco;

        }
    }
}

.servicios{
    @extend .citas;

    .acciones{
        margin: 0;        
    }

    .boton,
    .boton-eliminar{
        margin: 0 0 2rem 0;
    }
}